.background {
    z-index: -1;
    position: absolute;
    top: 7rem;
    left: 0;
    right: 0;
    margin: 0 auto;
    max-width: 1920px;
    background-color: #333;
  }
  
  .background-img {
    position: fixed;
    top: 0;
    left: 0;
    min-width: 1024px;
    width: 100%;
    min-height: 100%;
    height: auto;
  }
  
  @media screen and (max-width: 1024px) {
    /* Specific to this particular image */
    .background-img {
      left: 50%;
      margin-left: -512px; /* 50% */
    }
  }
  